.franchise {
  &-button {
    &__close {
      position: absolute;
      top: 12px;
      right: 12px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 50%;
      cursor: pointer;
    }
  }
  &-container {
    position: relative;
    overflow: none;
    overflow: scroll;
    border-radius: 10px;
    margin: 0 auto;
    border: 2px solid #f8d24e;
    background: #fcfcfc;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2em;
    padding: 28px 30px;
    @media screen and (max-width: 1199.9px) {
      max-width: 286px;
    }
    @media screen and (min-width: 1200px) {
      max-width: 574px;
    }
    margin-bottom: 2em;
    padding: 28px 30px;
    @media screen and (max-width: 1199.9px) {
      max-width: 286px;
    }
    @media screen and (min-width: 1200px) {
      max-width: 574px;
    }
  }
  &__title {
    margin-bottom: 20px;
    @media screen and (min-width: 1200px) {
      margin-bottom: 32px;
      max-width: 322px;
    }
    &--font {
      margin-top: 0;

      align-items: center;
      text-align: center;
      font-size: 16px;
      font-family: Seymour One;
      font-style: normal;
      font-weight: 400;
      line-height: 1.5;
      @media screen and (min-width: 1200px) {
        text-align: center;
        font-size: 20px;

        line-height: 1.4;
      }
    }
  }
  &__describe {
    @media screen and (min-width: 1200px) {
      max-width: 448px;
    }

    &--text {
      font-size: 16px;
      font-family: Montserrat;
      font-style: normal;
      font-weight: 400;
      line-height: 1.4;
    }
  }
}
.franchise-modal {
  padding-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
  &__label {
    display: flex;
    text-align: left;
    &:nth-last-child(2) {
      margin-bottom: 12px;
    }
  }
  &__input {
    padding: 8px 0 7px 12px;
    width: 100%;
    height: 100%;
    outline: rgba(0, 0, 0, 0);
    &:focus-within,
    &:focus-visible {
      outline: rgba(0, 0, 0, 0);
    }
    &--comment {
      width: 100%;
      height: 64px;
      padding: 12px 12px;

      outline: rgba(0, 0, 0, 0);
    }
  }
}
